<template lang="html">
  <div class="page" ref="popWrapper">
    <div class="content">
<a name="process171"></a>

<p><br></p>
<p><span class="rvts17">猫咪的宝藏（</span><span class="rvts50">The Cat's Treasure</span><span class="rvts17">）</span><span class="rvts50">[</span><span class="rvts17">支线</span><span class="rvts50">]</span><span class="rvts17">：</span></p>
<p><span class="rvts41">任务坐标：</span>
<br><span class="rvts20">X:285,Y:155</span><span class="rvts21">，</span><span class="rvts20">NPC</span><span class="rvts21">：小猫</span><span class="rvts20">Pox</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">目标：</span>
<br><span class="rvts21">获得猫咪的宝藏</span>
<br><span class="rvts40"></span>
<br><span class="rvts41">任务详情：</span>
<br><img width="100%" height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1175.jpg" display="block" background-size="contain|cover"><span class="rvts39"></span>
<br><span class="rvts21">在浮木城酒馆西边的一片空地上，可以找到一只叫做</span><span class="rvts20">Pox</span><span class="rvts21">的猫，与它交谈，并给它一些鱼，它会告诉你它的藏宝点。</span>
<br><span class="rvts39"></span>
<br><img width="100%" height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1176.jpg" display="block" background-size="contain|cover"><span class="rvts39"></span>
<br><span class="rvts21">然而令人费解的是，该藏宝点竟然需要</span><span class="rvts20">22</span><span class="rvts21">点的智慧值（</span><span class="rvts20">Wits</span><span class="rvts21">）才能发现，打开宝箱后里面是一些破袜子，破衣服。</span></p>
<p><br></p>
<p><br></p>

    </div>
  </div>
</template>
<script>
import BScroll from 'better-scroll'
export default {
  data () {
    return {};
  },
  methods: {
    _initScroll() {
      this.popScroll = new BScroll(this.$refs.popWrapper, {
        click: true,
        probeType: 3
      });
    }
  },
  created() {
    this.$nextTick(() => {
      this._initScroll();
    });
  }
};
</script>
<style>
@import '../../../../static/css/common.css';
</style>



